.highlighter-rouge div {
  margin-bottom: 1rem;
  border: 1px solid $primary;
  line-height: 100%;
}
.highlight table td { padding: 5px; }
.highlight table pre { margin: 0; }
.highlighter-rouge pre {
  background-color: white;
}
code {
  font-family: 'Ubuntu Mono';
}
code.highlighter-rouge {
  color: $grey-darker;
}
h3 code.highlighter-rouge {
  font-weight: bold;
}
.docs-image {
  margin-bottom: 1.3rem;
  margin-top: 1.3rem;
}
.content ul li {
  margin-bottom: 1em
}
.content h1 {
  padding-bottom: 0.4em;
  border-bottom: 1px solid;
}
.content p {
  margin: 0.7rem 0;
  line-height: 1.7;
  color: #3e3e3e;
}
.content blockquote {
  padding: .3rem 1.5rem;
  margin-bottom: 16px;
}

